@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,700,900&display=swap&subset=latin-ext');

body {
  background: #DEDEDE;
  margin: 0;
  padding: 0;
}

$margin: 2em;

.sp-main {    
  max-width: 1024px;
  margin: 0 auto;
  font-family: 'Roboto', sans-serif; 
  position: relative;
  background: #EEE;
  padding: 2em;
  height: auto;

  h1, h2 {
    font-family: 'Roboto', sans-serif; 
    font-weight: 700; 
    color: #345;    
  }

  h2 {
    margin-top: 2em;
  }

  p {    
    font-weight: 300;
    color: #444;
  }
  
  a {
    display: block;
    text-decoration: none;
    padding: .4em;
    color: orangered;


    &:hover {
      background:white;
    }
  }

  iframe {
    width: 100%;
    height: 55vw;
    border: none;
  }
}

.sp-image {  
  max-width: 100%;
  height: auto;  
}

.sp-remarks {
  color: rgb(28, 83, 138);
  margin: 2em;
  font-weight: 700;
}